.header-link {
 /* color: #100404; */
 position: relative;
 font-size: 1.3em;
}

.header-link:after {
 content: "";
 display: block;
 position: absolute;
 width: 0;
 left: 25%;
 bottom: 0;
 /* bottom: -3px; */
 /* border-bottom: 4px solid transparent; */
 border-bottom: 4px solid var(--theme-2);
 /* border-radius: 3px; */
 border-radius: 4px 4px 0 0;
 /* border-radius: 0 0 4px 4px; */
