 /* border-radius: 25px; */
}

.btn-main {
 background-color: var(--theme-2);
 background-image: linear-gradient(180deg, var(--theme-2-accent) 0%, var(--theme-2) 100%);
 color: var(--theme-1-accent);
 border: 1px solid var(--theme-2);
}

.btn-gold {
 background-color: var(--theme-3);
 background-image: linear-gradient(180deg, var(--theme-3-accent) 0%, var(--theme-3) 100%);
 color: var(--text-primary);
 border: 1px solid var(--theme-3);
}

.btn-white {
 background-color: var(--theme-1);
 background-image: linear-gradient(180deg, var(--theme-1-accent) 0%, var(--theme-1) 100%);
 color: var(--text-primary);
 border: 1px solid var(--theme-1);
}

